"use client";

import { useState } from "react";
import Navigation from "./navigation";
import { AlignRight, X } from "lucide-react";

const MobileNav = () => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <>
      <button
        onClick={() => setIsOpen(true)}
        className="md:hidden p-2 z-20 relative text-white"
        aria-label="Open Menu"
      >
        <AlignRight className="w-6 h-6" />
      </button>
      {isOpen && (
        <div className="fixed inset-0 z-50 bg-primary backdrop-blur-sm flex items-center justify-center transition-all duration-700">
          {/* Close Button */}
          <button
            onClick={() => setIsOpen(false)}
            className="absolute top-6 right-6 text-white"
            aria-label="Close Menu"
          >
            <X className="w-6 h-6" />
          </button>
          <div
            className={`flex-auto px-4 py-8 flex justify-center items-center`}
          >
            <Navigation />
          </div>
        </div>
      )}
    </>
  );
};

export default MobileNav;
